export default {
  computed: {
    menu() {
      return this.$store.state.menu;
    },
    mobileMenuVisibility() {
      return this.$store.state.menu.mobileMenuVisible;
    },
    menuItems() {
      return this.$store.state.menu.items;
    },
    social() {
      return this.$store.state.meta.social_media_links;
    },
  },
};
